export const DefaultSet = () => {
    return {
        repeats: 10,
        weight: 0,
        completed: false
    }
}

export const GenerateProgramExercise = id => {
    let setCount = 4;
    let sets = [];
    for (let i = 0; i < setCount; i++) {
        sets.push(DefaultSet())
    }
    return {
        id: id,
        sets: sets
    }
}

